<script context="module">
let _autoTestStart
export const  autoTestStart = (rootNode) => {
    _autoTestStart(rootNode)
}

</script>

<script>
import { importState, setState, event } from './store.js'
let layout = importState('layout')
import { onMount } from 'svelte'
import { loadPage } from './mainpanel.svelte'
let namespace = importState('namespace')
let user = importState('user')
let message = importState('message')
let mainTabs = importState('mainTabs')
let featuretestInstance = importState('featuretestInstance')
let el ;
let testingNodes = []

// render a list of nodes for testing
_autoTestStart = function(rootNode){
    if (typeof rootNode == 'undefined') rootNode = w2ui['LeftPanel']

    let nodesOfTest = []
    var digTestItems = function(node){
        if (node.nodes && node.nodes.length > 0){
            node.nodes.forEach((subnode) =>{
                digTestItems(subnode)
            })
        }else if (node.href){
            nodesOfTest.push({id:node.id, img:node.img, text:node.text, checked:true, href:node.href, completed:false, pass:false})
        }
    }    
    digTestItems(rootNode)
    testingNodes = nodesOfTest.slice()
}

function testNode(node){
    var  promise = new jQuery.Deferred()
    message.set('Testing '+node.href)
    loadPage(node.href).done(function(){
        $mainTabs.click('Test')
        $featuretestInstance.start_featuretest()
        .done(function(pass){
            promise.resolve(pass)
            node.pass = pass
            console.log(node.text,'pass=',pass)
        })
        .fail(function(xhr){
            console.log(node.text,'error=',xhr.responseText)
            promise.reject(xhr)
            node.pass = false
        })
    })
    return promise
}

function startTest(){
    var p = new jQuery.Deferred()
    var issue = function(i){
        if (i < testingNodes.length){
            if (testingNodes[i].checked){
                testNode(testingNodes[i]).always(function(){
                    testingNodes[i].completed = true
                    issue(i+1)
                })
            }else{
                testingNodes[i].completed = true
                issue(i+1)
            }
        }else{
            p.resolve()
        }
    }
    issue(0)
    return p
}

function goPage(evt){
    var nodeId  = evt.currentTarget.dataset.node
    testingNodes.some(function(node){
        if (node.id == nodeId) {
            loadPage(node.href)
            return true
        }
    })
}
</script>
<style>
.node {
}
.node.completed.pass{
    
}
.node.completed.no-pass{
    color:rgb(255, 123, 47);
}
</style>
<div style="padding:10px;">
    <h4>Auto Test</h4>
    <div>
        <button class="w2ui-btn" on:click={startTest}>Start</button>
    </div>
    <div bind:this={el}>
        {#each testingNodes as node}
            <div class="node {node.completed ? 'completed' : ''} {node.pass ? 'pass' : 'no-pass'}">
                {#if node.completed && node.pass}
                    <span class='fas fa-check'/>
                {:else if node.completed && !node.pass}
                    <span class='fas fa-skull-crossbones'/>
                {:else}
                    <input type="checkbox" bind:checked={node.checked}>
                {/if}
                 <a href="javascript:void(0)" style="text-decoration:none" data-node={node.id} on:click={goPage}>
                    {node.text}
                 </a>
            </div>
        {/each}
    </div>
</div>