function render(){
    jQuery.getJSON($config.sidebarNodes,(testing_nodes)=>{
        if (w2ui['LeftPanel']) w2ui['LeftPanel'].destroy()
        jQuery('.LeftPanel').w2sidebar({
            name: 'LeftPanel',
            menu:[
                { id: 'auto-test', text: 'Auto Testing', img: 'icon-page' },
            ],
            nodes: nodes.concat(testing_nodes),
            topHTML:`<div style="height:30px;width:100%;border-bottom:solid 1px #dcdcdc">
                <span style="color:#999;padding:10px;display:inline-block;cursor:pointer" class="doAutoTest-btn">
                    My Test Plan
                </span>
                <a style="color:#999;float:right;padding:10px;display:inline-block;cursor:pointer" onclick="app.event.fire(\'refresh-leftpanel\')">
                    <span class="fas fa-redo"> Refresh</span>
                </a>
                </div>`,
            onContextMenu:function(evt){
                var item = this.get(evt.target)
                if (!item.nodes || item.nodes.length == 0) {
                    evt.preventDefault()
                    return
                }
            },
            onMenuClick:function(evt){
                switch(evt.menuItem.id){
                    case 'auto-test':
                        doAutoTest(this.get(evt.target))
                        break
                }
                
            },
            onClick:function(evt){
                if (evt.node.href) {
                    loadPage(evt.node.href)
                }
            }
        });  
    }).fail(function(err){
        w2alert(err.responseText)
    })
}
event.on('refresh-leftpanel',()=>{
    render()
    message.set('sidebar refreshed')
})
user.subscribe(value =>{
    if (value == null) return
    username = $user.username
    if (username == 'guest'){
        //not been login or has logout
        if (w2ui['LeftPanel']) w2ui['LeftPanel'].destroy()
    }else{
        render()
    }
})

