<script>
import { importState } from './store.js'
import {onMount} from 'svelte'
export let expected 
export let options = {
    url:'http://localhost/index.html',
    validate:(record)=>{
        // called before getting content
        // if false returned, cancel the testing.
        // if this is missing, assume true
        return true
    },
    vefify:(response, xhr)=>{
        // called after success get content 
        return true //pass
    },
    fail:(error) => {
        // called if failed to get content
    },
    data:{} //for get or post (used for jquery)
}
let el
let className
let tree = importState('tree')
export let myself 

let expectedLintted = ''
let expects = { // expected values defined in loaded htmls
    status:200,
    header:[],
    response:[]
}
const expectsChecker =  (response, xhr) => {
    if (expects.status && !(xhr.status == expects.status)) return false
    let pass = true
    if (expects.header.length){
        let allHeaders = xhr.getAllResponseHeaders()
        expects.header.some(function(re){
            if (!allHeaders.match(re)){
                console.warn('header error')
                pass = false
                return true
            }
        })
        if (!pass) return pass
        console.log('header check ok')
    }
    if (expects.response.length){

        if (typeof response == 'object'){
            // covert object to json if necessary
            response = JSON.stringify(response)
        }
        
        expects.response.some(function(re){
            if (!response.match(re)){
                console.warn('response error',response,re)
                pass = false
                return true
            }
        })
        if (!pass) return pass
        console.log('response check ok')
    }
    return pass
}

onMount(async ()=>{
    // convert <expected> to pretty DOM element
    if (expected){
        let lintExpectedLines =  []
    
        let lines = expected.innerHTML.split('\n')        
        lines.forEach(function(line){
            line = line.trim()
            if (line == '') return
            // add newline to every pure-text line
            if (line.substr(0,1) != '<') line += '\n'
            lintExpectedLines.push(line)
        })
        // collect expected value
        if (expected.querySelector('status')) expects.status = parseInt(expected.querySelector('status').innerText)
        expected.querySelectorAll('header').forEach(function(ele){
            var text = ele.innerText.trim()
            if ((/^\/.+\/$/).test(text)) {
                var m = text.match(/^\/(.+)\/$/)
                expects.header.push(new RegExp(m[1]))
            }else{
                expects.header.push(new RegExp(text))
            }            
        })
        expected.querySelectorAll('response').forEach(function(ele){
            var text = ele.innerText.trim()
            if ((/^\/.+\/$/).test(text)) {
                var m = text.match(/^\/(.+)\/$/)
                expects.response.push(new RegExp(m[1]))
            }else{
                expects.response.push(new RegExp(text))
            }
        })
        expectedLintted = lintExpectedLines.join('')
        //convert <status to <div class="status
        var tags2convert = ['status','header','response']
        tags2convert.forEach(function(tag){
            expectedLintted = expectedLintted.replace(new RegExp('<'+tag,'g'),'<div class="'+tag+'"')
            expectedLintted = expectedLintted.replace(new RegExp('</'+tag+'>','g'),'</div>')
        })
        expected.remove()
    }
   
    el.className.split(' ').some(function(n){
        if (/^svelte/.test(n)) {className = n; return true}
    })
    
    if ($tree){
        render()
    }
})
const prefix = 'featuretest'
const render = () =>{

    const fields = [
            { name:'url',field: 'url', type: 'textarea', required: true ,html:{caption:'URL To Test', attr:'cols="100" placeholder="http://" style="width:98%;height:40px"'}},
        ]
    const record = {
        url:options.url,
    }
    // name will be converted to be "id" of <input>,
    // so to set a unique name is necessary
    fields.forEach((f)=>{
        f.name = prefix + f.name
    })
    let urecord = {}
    for (let name in record){
        urecord[prefix+name] = record[name]
    }

    if (w2ui['featuretest-form']) w2ui['featuretest-form'].destroy()
    jQuery(el.querySelector('.form')).w2form({ 
        name     : 'featuretest-form',
        //header   : 'Feature Test',
        style:'width:100%',
        fields: fields,
        record:urecord,
        actions: {
            reset: function () {
                this.clean();
            },
            save: function () {
                start_featuretest()                
            }
        }
    });
    el.querySelector('button[name="save"]').innerHTML = 'Run'
    el.querySelector('button[name="reset"]').style.display = 'none'
}

tree.subscribe(tree =>{
    if ($tree == null){
        if (w2ui['featuretest-form']) w2ui['featuretest-form'].destroy()
        return
    }else if (el){
        render()
    }    
})

export const start_featuretest = () =>{
    var promise = new jQuery.Deferred()
    var record = {}
    var len = prefix.length
    var theform = w2ui['featuretest-form']
    if (!theform) {
        promise.reject('invalid form')
        return promise
    }
    for(let name in theform.record){        
        record[name.substr(len)] = theform.record[name]
    }
    if ((options.validate) &&  !options.validate(record)){
        promise.reject('invalid record')
        return promise
    }

    w2ui['featuretest-form'].lock('Testing',true)
    var url = record.url
    var query = {
        url:url,
        data:options.data,
        dataType:options.dataType
    }
    if (options.responseType){
        query.xhr = function(){
            var xhr = new XMLHttpRequest();
            xhr.responseType= options.responseType
            return xhr;
        }
    }
    jQuery.get(query)
    .done(function(response,status, xhr){
        el.querySelector('.result.http-response').style.display = 'block'        
        el.querySelector('.result.http-response').innerHTML = '<pre class="header">' + xhr.status+ ' ' + xhr.statusText + '\n' + xhr.getAllResponseHeaders() + '</pre><pre class="response">' + (options.renderResponse ? options.renderResponse(response) : response) + '</pre>'
        var pass = (options.verify) ? options.verify(response, xhr) : expectsChecker(response,xhr)
        el.querySelector('.result.http-response').classList.add(pass ? 'pass' : 'no-pass')
        el.querySelector('.result.http-response').classList.remove(pass ? 'no-pass' : 'pass')
        promise.resolve(pass)
    })
    .fail(function(xhr){
        el.querySelector('.result.http-response').style.display = 'block'
        el.querySelector('.result.http-response').classList.remove('pass')
        el.querySelector('.result.http-response').classList.add('no-pass')
        el.querySelector('.result.http-response').innerHTML = '<pre class="header">' + xhr.status+ ' ' + xhr.statusText + '\n' + xhr.getAllResponseHeaders() + '</pre><pre class="response">' + xhr.responseText + '</pre>'
        promise.reject(xhr)
    })
    .always(()=>{
        w2ui['featuretest-form'].unlock()
    })
    return promise
}
</script>
<style>

/* Testing related starts */
:global(.http-response.expected){
    text-align:left;
    white-space:pre;
    background-color:white !important;
    border-radius:5px;
    padding-left:15px;
    padding-bottom:10px;
}
:global(.http-response.expected::before){
    content:"Expected";
    padding:10px;
    margin-left:-15px;
    display:block;
    border-bottom:solid 1px #c4c2c2;
    margin-bottom:10px;
}
:global(.http-response.expected .status){
    font-weight: bold;
}
:global(.http-response.expected .header){
    font-weight: bold;
}
:global(.http-response.expected .response){
    margin-top:1em;
    font-weight: bold;
}

:global(.http-response){
    font-family: monospace;
    white-space: pre;
    width:100%;
    border-radius:5px;
    padding:0px;
    border:solid 1px #dbdbdb;  

}
:global(.http-response.result){
    display:none;

}
:global(.http-response.result::before){
    content:"Result";
    padding:10px;
    display:block;
    border-bottom:solid 1px #c4c2c2;
    margin-bottom:10px;
}
:global(.http-response pre){
    width:100%;
    padding:10px;
}
:global(.http-response .header){
    /* 載入的 page 當中會有動態增加的內容，所以用 :global() */
    color:#666;
}
:global(.http-response .response){
    /* 載入的 page 當中會有動態增加的內容，所以用 :global() */
}
:global(.http-response.pass){
}
:global(.http-response.pass::after){
    content: "PASS";
    margin-left: 2px;
    background-color: #46fd3b;
    padding: 0px 10px;
    display: inline-block;
    line-height: 20px;
    font-size: 14px;
}
:global(.http-response.no-pass){
}
:global(.http-response.no-pass::after){
    content: "NO-PASS";
    margin-left: 2px;
    color:white;
    background-color: #fd583b;
    padding: 0px 10px;
    display: inline-block;
    line-height: 20px;
    font-size: 14px;
    border-radius: 5px;
}

/* Testing related end */
</style>
<div>
    <slot>
        <div bind:this={el}>
            <table style="width:100%;vertical-align:top">
                <tr>
                    <td style="vertical-align:top"><div class="form"/></td>
                    <td style="width:50%;vertical-align:top"><div class="http-response expected">{@html expectedLintted}</div></td>
                </tr>
                <tr>
                    <td></td>
                    <td style="vertical-align:top"><div class="result http-response"></div></td>
                </tr>

            </table>
            
        </div>
    </slot>
    <!-- let svelte compiler to include related css -->
    <div style="display:none" class="http-response pass no-pass expected"><pre></pre></div>
</div>
